@import url(https://fonts.googleapis.com/css?family=Allura&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F3F2F2 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.common-app {
    color: #515C6F;
    font-family: 'Playfair Display', serif;
}
.portfolio-page {
    width: 80%;
    margin: 100px 10% 30px;
}
.primary-button-display {
    border-radius: 19px !important;
    background-color: #FF6969 !important;
    border-color: #FF6969 !important;
    box-shadow: 2px 2px 8px #FF6969;
    color: white;
    height: 40px;
    font-family: 'Roboto', sans-serif !important;
    font-weight: bold !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .portfolio-page {
        width: 80%;
        margin: 80px 10%;
    }
}

.background-with-aga {
    position: relative;
    margin-top: 56px;
}
.overlay-box {
    position: absolute;
    top: 10%;
    left: 5%;
    padding-top: 15px;
    padding-right: 15px;
    padding-left: 15px;
    padding-bottom: 30px;
    min-width: 300px;
    width: 35%;
    background: rgba(196, 196, 196, 0.7);
    color: #2D394E;
}
.overlay-title {
    font-size: calc(40px + 1vw);
}
.overlay-subtitle {
    font-size: calc(26px + 1vw);
    margin-bottom: 5px;
}
.overlay-description {
    font-size: calc(8px + 1vw);
    font-family: 'Roboto', sans-serif;
    font-weight: lighter;
    margin-bottom: 30px;
}
.portfolio-item {
    width: 80%;
    height: 80%;
    box-shadow: 1px 1px 3px #c2c1c1;
}
.portfolio-cover {
    margin-top: 2.5%;
    margin-bottom: 2.5%;
    position: relative;
}
.portfolio-image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    -webkit-transition: .5s ease;
    transition: .5s ease;
}
.hovered-filter {
    margin-left: 10%;
    width: 80%;
    height: 100%;
    top: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    background-color: #FF6969;
}
.portfolio-cover:hover .portfolio-image {
    opacity: 0.7;
}
.portfolio-cover:hover .hovered-filter {
    opacity: 0.7;
}
.portfolio-text {
    position: absolute;
    margin-top: 10%;
    margin-left: 50%;
    margin-right: 5%;
    font-size: 5vw;
    color: white;
    text-align: right;
}
.portfolio-details {
    font-size: 2.4vw;
}

@media all and (min-width: 700px) and (max-width: 1000px) {
    .overlay-box {
        width: 45%;
    }
}

@media all and (min-width: 0px) and (max-width: 700px) {
    .overlay-box {
        position: relative;
        left: 0;
        padding: 10%;
        min-width: 300px;
        width: 100%;
        background-color: #F3F2F2;
    }
    .overlay-description {
        font-size: 16px;
    }
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .portfolio-item {
        width: 100%;
        height: 100%;
    }

    .portfolio-image {
        opacity: 1;
    }

    .portfolio-text {
        background-color: #FF6969;
        opacity: 0.7;
        padding-right: 5%;
    }

    .portfolio-details {
        margin-bottom: 0;
        font-size: 3.8vw;
        line-height: 125%;
    }

    .hovered-filter {
        opacity: 1;
        background-color: transparent;
        margin-left: 0;
        width: 100%;
    }
}
.about-me-picture {
    width: 80%;
}
.about-me-title {
    font-size: calc(18px + 3vw);
    text-align: center;
}
.about-me-text {
    font-size: calc(12px + 0.6vw);
    margin: 40px 10%;
    text-align: left;
}
.about-me-text p {
    margin-bottom: 30px;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .about-me-picture {
        width: 100%;
    }

    .about-me-text {
        margin: 40px 0;
    }
}
.project-title {
    text-align: center;
    padding: 0 10%;
}

.project-title h1 {
    padding-bottom: 20px;
}

.project-title-image {
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 1px 1px 3px #c2c1c1;
}

.project-section {
    margin: 60px 0;
}

.project-subsection {
    margin: 20px 0;
}

.project-single-picture {
    display: table-caption;
    width: 80vw;
    margin-bottom: 10px;
}

.picture-with-description {
    display: table-caption;
    margin: 10px;
}

.image-shading {
    display: block;
    box-shadow: 1px 1px 3px #c2c1c1;
    margin-left: auto;
    margin-right: auto;
}

.image-no-shading {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.single-picture-normal {
    width: 35%;
}

.single-picture-xlrg {
    width: 65%;
}

.single-picture-fullsize {
    width: 100%;
}

.image-description {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.multi-picture-spacing {
    padding: 10px;
}

.skip-button {
    text-align: center;
    margin: 20px 0 60px;
}

.recommendation-picture {
    width: 100%;
    min-width: 250px;
    margin-bottom: 20px;
    box-shadow: 1px 1px 3px #c2c1c1;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .project-title {
        padding: 0;
    }

    .project-title h3 {
        font-size: 1.4rem;
    }

    .single-picture-normal {
        width: 60%;
    }

    .single-picture-xlrg {
        width: 100%;
    }
}

@media all and (min-width: 501px) {
    .project-subsection p {
        font-size: 25px;
    }

    .project-section p {
        font-size: 25px;
    }
}
.resume-title {
    margin-top: 5%;
    margin-bottom: 2.3%;
    font-size: calc(18px + 1vw);
}
.resume-text {
    margin-bottom: 50px;
    font-size: calc(14px + 0.4vw);
}
.resume-footer {
    margin-left: 0 !important;
    padding-left: 0 !important;
}
.resume-footer a {
    color: #515C6F;
    padding-right: 100px;
}
.contact-page-container {
    margin: 0 !important;
}
.details-contact-form {
    max-width: 500px !important;
    min-width: 240px !important;
}
.form-group-display {
    background-color: white;
    box-shadow: 1px 1px 3px #c2c1c1;
    border-radius: 10px;
}
.icon-display {
    padding: 25px !important;
}
.no-icon-display {
    padding: 39px !important;
}
.form-display {
    padding: 0 !important;
    width: calc(100% - 84px) !important;
}
.label-display {
    margin-top: 8px !important;
    margin-bottom: 0 !important;
}
.input-display {
    border-color: lightgray !important;
    padding: 0 !important;
    margin-bottom: 2px !important;
    font-size: calc(12px + 0.2vw) !important;
    min-height: 40px;
}
.contact-me-button {
    box-shadow: 2px 2px 8px #FF6969;
    border-radius: 19px !important;
    background-color: #FF6969 !important;
    border-color: #FF6969 !important;
    min-width: 110px;
    width: 12vw;
    font-family: 'Roboto';
}
.details-title h3 {
    padding-bottom: 5px;
}
.details-content {
    padding-bottom: 30px;
    text-align: right;
}
.toast-display {
    width: 30vw;
}
.alert-fixed {
    position:fixed;
    top: 65px;
    left: 0;
    width: 100%;
    z-index:9999;
    border-radius:0
}
.navbar-name {
    vertical-align: middle;
    font-family: 'Allura', cursive;
    font-size: 15pt;
    letter-spacing: 0.1em;
    padding-left: 30px;
}
.navbar {
    background-color: #F3F2F2;
    border-bottom: 1px solid #D7D0D0;
}
.navbar a {
    color: #515C6F;
    font-size: 20px;
    margin-right: 50px;
}
.sub-navbar {
    background-color: #F6F6F6 !important;
    font-size: 10px !important;
    width: 170px;
}
.sub-navbar a {
    font-size: 18px !important;
}

.logo-copyright-display {
    display: grid;
}

.logo-display {
    width: 130px;
}

.footer-display {
    width: 80%;
    margin: 0 10% 50px;
    display: block;
}

.tall-lines {
    line-height: 300%;
}

.contact-details-display {
    line-height: 300%;
}

@media all and (min-width: 0px) and (max-width: 575px) {
    .contact-details-display {
        margin: 12px 0;
        line-height: 150%;
    }
}
